import {Switch} from '@headlessui/react'

export default function Toggle({isGreen, toggleGreen}) {

    return (
        <Switch
            checked={isGreen}
            onChange={toggleGreen}
            className={`${
                isGreen ? 'bg-green-800' : 'bg-brand-toggle opacity-30'
            } relative inline-flex items-center h-6 rounded-full w-11`}
        >
            <span className="sr-only">Toggle Green Mode</span>
            <span
                className={`${
                    isGreen ? 'translate-x-6' : 'translate-x-1'
                } inline-block w-4 h-4 transform bg-white rounded-full`}
            />
        </Switch>
    )
}
