import {Disclosure} from '@headlessui/react'
import {BellIcon, MenuIcon, XIcon} from '@heroicons/react/outline'
import Products from "./components/products/Products";
import olxLogo from './assets/olx_logo.png'
import olxGreen from './assets/olx_green.svg'
import {Link, Route, Switch} from "react-router-dom";
import Product from "./components/products/Product";
import Footer from "./components/footer/Footer";
import GoGreenBanner from "./components/goGreenBanner/GoGreenBanner";
import {useEffect, useState} from "react";
import LeafWhite from "./assets/leaf_white";
import Checkout from "./components/checkout/Checkout";

const navigation = [
    {name: 'Search', href: '/', current: true},
    {name: 'Products', href: '/', current: false},
    {name: 'Categories', href: '/', current: false},
]

function classNames(...classes) {
    return classes.filter(Boolean).join(' ')
}

export default function App() {
    const [isGreen, setIsGreen] = useState(JSON.parse(localStorage.getItem("isGreen")))
    useEffect(() => {
        localStorage.setItem("isGreen", `${isGreen}`)
    }, [isGreen])

    return (
        <div className="min-h-screen bg-white">
            <Disclosure as="nav" className="bg-white border-b border-gray-200">
                {({open}) => (
                    <>
                        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
                            <div className="flex justify-between h-16">
                                <div className="flex">
                                    <div className="flex-shrink-0 flex items-center">
                                        <Link to="/"><img
                                            className="block lg:hidden h-8 w-auto"
                                            src={isGreen ? olxGreen : olxLogo}
                                            alt="OLX Green"
                                        />
                                            <img
                                                className="hidden lg:block h-8 w-auto"
                                                src={isGreen ? olxGreen : olxLogo}
                                                alt="OLX Green"
                                            /></Link>
                                    </div>
                                    <div className="hidden sm:-my-px sm:ml-6 sm:flex sm:space-x-8">
                                        {navigation.map((item) => (
                                            <a
                                                key={item.name}
                                                href={item.href}
                                                className={classNames(
                                                    item.current
                                                        ? `${isGreen ? 'border-brand-primary text-gray-900' : 'border-brand-primaryDefault text-gray-900'}`
                                                        : 'border-transparent text-gray-500 hover:border-gray-300 hover:text-gray-700',
                                                    'inline-flex items-center px-1 pt-1 border-b-4 text-sm font-medium'
                                                )}
                                                aria-current={item.current ? 'page' : undefined}
                                            >
                                                {item.name}
                                            </a>
                                        ))}
                                    </div>
                                </div>
                                <div className="hidden sm:ml-6 sm:flex sm:items-center">
                                    <button
                                        type="button"
                                        className="bg-white p-1 rounded-full text-gray-400 hover:text-gray-500 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500"
                                    >
                                        <span className="sr-only">View notifications</span>
                                        <BellIcon className="h-6 w-6" aria-hidden="true"/>
                                    </button>
                                    {isGreen && <Link
                                        to="/"
                                        className="flex items-center justify-center px-4 py-2 border border-transparent rounded-md shadow-sm text-sm bg-brand-secondary"
                                    >
                                        <span className="w-5 h-5 bg-brand-primary rounded-full flex items-center justify-center mr-2"><LeafWhite /></span>
                                        <span className="text-brand-primary mr-2">10.000</span>
                                        <span className="font-bold">Green Coins</span>
                                    </Link>}
                                </div>
                                <div className="-mr-2 flex items-center sm:hidden">
                                    {/* Mobile menu button */}
                                    <Disclosure.Button
                                        className="bg-white inline-flex items-center justify-center p-2 rounded-md text-gray-400 hover:text-gray-500 hover:bg-gray-100 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500">
                                        <span className="sr-only">Open main menu</span>
                                        {open ? (
                                            <XIcon className="block h-6 w-6" aria-hidden="true"/>
                                        ) : (
                                            <MenuIcon className="block h-6 w-6" aria-hidden="true"/>
                                        )}
                                    </Disclosure.Button>
                                </div>
                            </div>
                        </div>

                        <Disclosure.Panel className="sm:hidden">
                            <div className="pt-2 pb-3 space-y-1">
                                {navigation.map((item) => (
                                    <a
                                        key={`${item.href}${item.name}`}
                                        href={item.href}
                                        className={classNames(
                                            item.current
                                                ? 'bg-indigo-50 border-indigo-500 text-indigo-700'
                                                : 'border-transparent text-gray-600 hover:bg-gray-50 hover:border-gray-300 hover:text-gray-800',
                                            'block pl-3 pr-4 py-2 border-l-4 text-base font-medium'
                                        )}
                                        aria-current={item.current ? 'page' : undefined}
                                    >
                                        {item.name}
                                    </a>
                                ))}
                            </div>
                        </Disclosure.Panel>
                    </>
                )}
            </Disclosure>
            <GoGreenBanner toggleGreen={setIsGreen} isGreen={isGreen}/>

            <main>
                <div className="max-w-7xl mx-auto sm:px-6 lg:px-8">
                    {/* Replace with your content */}
                    <div className="px-4 py-8 sm:px-0">
                        <Switch>
                            <Route path="/" exact>
                                <Products isGreen={isGreen}/>
                            </Route>
                            <Route path="/products/:productId" exact>
                                <Product isGreen={isGreen}/>
                            </Route>
                            <Route path="/checkout/:productId" exact>
                                <Checkout isGreen={isGreen}/>
                            </Route>
                        </Switch>
                    </div>
                    {/* /End replace */}
                </div>
            </main>
            <Footer isGreen={isGreen}/>
        </div>
    )
}
