import img1 from '../../assets/phones/image 1.png'
import img2 from '../../assets/phones/image 2.png'
import img3 from '../../assets/phones/image 3.png'
import img4 from '../../assets/phones/image 4.png'
import img5 from '../../assets/phones/image 5.png'
import img6 from '../../assets/phones/image 6.png'
import img7 from '../../assets/phones/image 7.png'
import img8 from '../../assets/phones/image 8.png'
import img9 from '../../assets/phones/image 9.png'
import img10 from '../../assets/phones/image 10.png'
import ac1 from '../../assets/ac/1.png'
import ac2 from '../../assets/ac/2.jpeg'
import ac3 from '../../assets/ac/3.jpeg'
import ac4 from '../../assets/ac/4.jpeg'
import wm1 from '../../assets/washing_machines/1.jpeg'
import wm2 from '../../assets/washing_machines/2.jpeg'
import wm3 from '../../assets/washing_machines/3.jpeg'
import wm4 from '../../assets/washing_machines/4.jpeg'

export default function ProductImage(type = "phone") {
    if (type === "AC") {
        const acArray = [ac1, ac2, ac3, ac4];
        return acArray[Math.floor(Math.random() * 3)]
    }
    if (type === "washing machine") {
        const acArray = [wm1, wm2, wm3, wm4];
        return acArray[Math.floor(Math.random() * 3)]
    }
    const imagesArray = [img1, img2, img3, img4, img5, img6, img7, img8, img9, img10];
    return imagesArray[Math.floor(Math.random() * 10)]
}