import {Link} from "react-router-dom";
import productsData from '../../assets/mock_data.json';
import coinsBanner from '../../assets/banners_product-listing/4.svg';
import goGreenBanner from '../../assets/banners_product-listing/2.svg';
import goGreenBannerLarge from '../../assets/banners_product-listing/3.svg';
import coinsBannerSmall from '../../assets/banners_product-listing/1.svg';
import ProductImage from "./ProductImages";
import {Fragment} from "react";

export default function Products({isGreen}) {


    return (
        <div className="bg-white">
            <div className="max-w-2xl mx-auto py-4 px-4 sm:py-8 sm:px-4 lg:max-w-7xl lg:px-4">
                <div className="mt-6 grid gap-y-10 gap-x-6 sm:grid-cols-2 lg:grid-cols-4 xl:gap-x-8">
                    {productsData.filter((p) => p.prod_title !== null).map((product, index) => (
                        <Fragment key={`${product.ad_id}-key`}>
                            {index === 5 && isGreen && <div key={`${product.ad_id}-1`} className="col-span-2"><img src={coinsBanner} alt="Coin Banner" className="w-full"/></div>}
                            {index === 5 && !isGreen && <div key={`${product.ad_id}-3`} className="col-span-2"><img src={goGreenBannerLarge} alt="Coin Banner" className="w-full"/></div>}
                            {index === 13 && !isGreen && <div key={`${product.ad_id}-2`} className=""><img src={goGreenBanner} alt="Coin Banner" className="w-full"/></div>}
                            {index === 18 && isGreen && <div key={`${product.ad_id}-4`} className=""><img src={coinsBannerSmall} alt="Coin Banner" className="w-full"/></div>}
                            <div key={product.ad_id} className="group relative">
                                <div
                                    className="w-full min-h-80 bg-gray-200 aspect-w-1 aspect-h-1 rounded-md overflow-hidden group-hover:opacity-75 lg:h-80 lg:aspect-none">
                                    <img
                                        src={ProductImage(product.archtype)}
                                        alt={product.imageAlt}
                                        className="w-full h-full object-center object-cover lg:w-full lg:h-full"
                                    />
                                </div>
                                <div className="mt-4 flex justify-between">
                                    <div>
                                        <h3 className="text-sm text-gray-700">
                                            <Link to={`/products/${product.ad_id}`}>
                                                <span aria-hidden="true" className="absolute inset-0"/>
                                                {product.prod_title}
                                            </Link>
                                        </h3>
                                        <p className="mt-1 text-sm text-gray-500">{product.prod_category_l1} {product.prod_category_l2}</p>
                                    </div>
                                    <p className="text-sm font-medium text-gray-900 w-12">&euro; {product.prod_price}</p>
                                </div>
                            </div>
                        </Fragment>
                    ))}
                </div>
            </div>
        </div>
    )
}

